import React, { Component } from 'react'
import data from './modules.json'
import logo from '../logo.png';
import '../App.css';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { Link } from "react-router-dom";
import ModuleOneB from "./Moduleoneb.png"
import ModuleOneB2 from "./Moduleoneb2.png"
import ModuleOneB3 from "./Moduleoneb3.png"

const theme = createTheme({
    palette: {
        primary: {
            main: '#068238',
        },
        secondary: {
            main: '#000000',
        },
        tertiary: {
            main: '#FFA800',
        }
    },
});
let page = 0
let module_text = data[page].text
let module_name = data[page].name

function DisplayImg(props){
    return <img src={ModuleOneB}/>
}
function DisplayImg2(props){
    return <img src={ModuleOneB2}/>
}
function DisplayImg3(props){
    return <img src={ModuleOneB3}/>
}
function ImageReturn(props){
    const isDisplayed = props.isDisplayed;
    if(isDisplayed){
        return <DisplayImg />
    }
    else{
        return <div></div>
    }
}
function ImageReturn2(props){
    const isDisplayed2 = props.isDisplayed2;
    if(isDisplayed2){
        return <DisplayImg2 />
    }
    else{
        return <div></div>
    }
}
function ImageReturn3(props){
    const isDisplayed3 = props.isDisplayed3;
    if(isDisplayed3){
        return <DisplayImg3 />
    }
    else{
        return <div></div>
    }
}

export default class Main extends Component {
    constructor(props) {
        super(props);
        this.nextPage = this.nextPage.bind(this);
        this.state = {isDisplayed:false}
        this.state = {isDisplayed2:false}
        this.state = {isDisplayed3:false}
      }
      
      nextPage() {
        page = page + 1;
        document.getElementById("moduleonename").innerHTML = data[page].name;
        document.getElementById("moduleonetext").innerHTML = data[page].text;      
       
       if(page==1){ this.setState({isDisplayed:true})}
       else{
           this.setState({isDisplayed:false})
       }
       if(page==2){ this.setState({isDisplayed2:true})}
       else{
           this.setState({isDisplayed2:false})
       }
       if(page==3){ this.setState({isDisplayed3:true})}
       else{
           this.setState({isDisplayed3:false})
       }
    }
    render() {
        const isDisplayed = this.state.isDisplayed;
        const isDisplayed2 = this.state.isDisplayed2;
        const isDisplayed3 = this.state.isDisplayed3;

        return (<ThemeProvider theme={theme}>

            <div class="home-page-header">
                <img alt="logo" src={logo} class="logo" />
                <div class="buttons">
                    <Stack spacing={2} direction="row">
                        <Link to="/"><Button variant="text" color="secondary">Home</Button></Link>
                        <Link to="/etrainingHome"><Button variant="contained">E-Training</Button></Link>
                        <Button variant="text" color="secondary">Job Portal</Button>
                    </Stack>
                </div>
            </div>
            <hr class="line-break2" />
            <div class="module-page-title" id="moduleonename">
                {module_name}

            </div>
            <div class="module-text" id="moduleonetext">
                {module_text}
            </div>
         
            
            <ImageReturn isDisplayed = {isDisplayed}/>
            <ImageReturn2 isDisplayed2 = {isDisplayed2}/>
            <ImageReturn3 isDisplayed3 = {isDisplayed3}/>

            <div class="module-button">
                <Button variant="contained" onClick={this.nextPage} >Melanjutkan</Button>
            </div>

            <div class="footer">Kerjaku.org <br /> Telp : 024 672 4835 / 024 672 4865 <br /> Email : ykks.semarang19@gmail.com</div>

        </ThemeProvider >

        )
    }
}